@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,400;0,700;1,400&display=swap');
@import url('../normalize.css');

$views: 'velocity', 'power', 'weight';
$languages: 'HU', 'EN';
$measurements: 'metric', 'imperial';

html {
    height: 100%;
    background-image: linear-gradient(180deg, rgba(255,255,255,0.4) 0%, rgba(0,0,0,0.4) 100%);
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: 50% 50%;
}
body {
}

.FpsConverter {
    font-family: 'Rubik', sans-serif;
    font-size: 24px;
    margin: 24px;
}

.Panel {
    display: flex;
    margin: 16px 0;
}

.CalculatorPanel {
    display: none!important;
    padding: 16px;
    border-radius: 8px;
    background: rgba(255,255,255,.4);

    @each $view in $views {
        .FpsConverter[data-view="#{$view}"] &[data-view="#{$view}"] {
            display: revert!important;
        }
    }
}
.CalculatorPanelBlock {
    display: flex;
    margin: 16px 0;
    flex-direction: row;
    flex-wrap: wrap;
}
.CalculatorPanelLabel, .CalculatorPanelInput {
    flex-basis: 100%;
}
.CalculatorPanelUnit {
    display: block;
    flex-basis: 100%;
    font-size: 75%;
    font-style: italic;
    color: #444;
}
.CalculatorPanelOutput {
    font-size: 125%;
    font-weight: bold;
}

.ViewSelector {
    flex-direction: column;
}
.ViewSelector-title {
    font-weight: 700;
    margin-bottom: 8px;
}
.ViewSelector-select {
    flex-basis: 100%;
    padding: 8px;
    font-size: 24px;
}

.SettingsPanel {
    background: #fff;
    box-sizing: border-box;
    padding: 8px;
    box-shadow: -2px 0 5px 0 rgba(0,0,0,.2);    
    position: fixed;
    bottom: -14vh;
    left: 0;
    width: 100vw;
    height: 14vh;
    display: flex;
    gap: 1vw;
    flex-direction: column;
    transition: bottom 400ms cubic-bezier(0.69, -0.43, 0.63, 1.45);

    &[data-opened=true] {
        bottom: 0;
    }
}
.SettingsPanelToggler {
    position: absolute;
    width: 40px; height: 40px;
    top: -60px; right: 16px;
    border-radius: 8px;
    background: yellow;
}

.LanguageSelector, .MeasurementSelector {
    display: flex;
    flex-direction: row;
    gap: 1vw;
}
.LanguageSelectorItem, .MeasurementSelectorItem {
    flex-basis: 50%;
    height: 5vh;
    line-height: 5vh;
    text-align: center;
    background: #ddd;
    border-radius: 5px;
}
.LanguageSelectorItem {
    @each $language in $languages {
        .LanguageSelector[data-lang="#{$language}"] &[data-lang="#{$language}"] {
            font-weight: bold;
        }
    }
}
.MeasurementSelectorItem {
    @each $measurement in $measurements {
        .MeasurementSelector[data-measurement="#{$measurement}"] &[data-measurement="#{$measurement}"] {
            font-weight: bold;
        }
    }
}
